import { createAsyncThunk } from '@reduxjs/toolkit';
import { APIRoute, AppRoute } from '../../const';
import { dropToken, saveToken } from '../../services/token';
import { redirectToRoute } from '../middlewares/redirect';
import { dropUser, saveUser } from '../../services/user';
export const checkAuthAction = createAsyncThunk('users/checkAuth', async (_arg, { extra: api }) => await api.get(APIRoute.Login));
export const loginAction = createAsyncThunk('users/login', async ({ body, errorHandler }, { dispatch, extra: api, rejectWithValue }) => {
    try {
        const { data } = await api.post(APIRoute.Login, body);
        saveToken(data.token);
        saveUser(data.user);
        dispatch(redirectToRoute(AppRoute.Main));
    }
    catch (err) {
        let error = err;
        if (!error.response) {
            throw err;
        }
        errorHandler(error.response.data);
        return rejectWithValue(error.response.data);
    }
});
export const logoutAction = createAsyncThunk('users/logout', async (_arg, { extra: api }) => {
    await api.delete(APIRoute.Logout);
    dropToken();
    dropUser();
});
